var _self
export default {
	data() {
		
		return{
			
			news :[]
		}
	},
	onLoad:function(e){
		_self=this;
		_self.getNews();
		
		
	},
	methods: {
			
		getNews:function(){
			uni.request({
				url:"http://192.168.1.143:8080/Notice/selectAll",
				method:"POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				success:function(res){
			 		console.log(res);
					_self.news=res.data.data;  
					
				}
				
				})
			
		}
		
	}
}


